
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import showMore from '@/components/ShowMore/showMore'  
export default {
components:{
XHeader,
Collapse,
showMore
},
data() {
return {
TYPE: "",
CBH: "",
info: {},
}
},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.TYPE = routerParams.TYPE;
this.CBH = routerParams.CBH;
this.getChildbirthInfo();
},

getChildbirthInfo() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "fenmianService",
"MSH.2": "getById",
"TYPE": this.TYPE,
"CBH": this.CBH,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
console.log(res);
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},

getScreeningResult(BM) {
switch (BM) {
case 1:
return "未做";
break;
case 2:
return "通过";
break;
case 3:
return "需复查";
break;
default:
return "";
break;
}
},

getVaccinationResult(BM) {
switch (BM) {
case 1:
return "未接种";
break;
case 2:
return "已接种";
break;
default:
return "";
break;
}
}
}
}



